.CatView {
    width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden
}

.CatView__name {
    color: darkblue;
    position: absolute;
    left: 10px;
    top: 0;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}

.CatView__model {
    height: 100%;
    padding: 5px 0;
    transition: all .5s ease;
}

.CatView__model--eyes {
    transform: scale(2.5) translate(-50px, 200px);
    transition: all .5s ease-out;
}

.CatView__model--necklace {
    transform: scale(2.5) translate(-50px, 50px);
    transition: all .5s ease-out;
}

.CatView__model--hat {
    transform: scale(2.5) translate(-50px, 350px);
    transition: all .5s ease-out;
}
.AttributeItem {
    width: 100%;
    margin: 8px 5px;
    background: #ACACAC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.AttributeItem:hover {
    cursor: pointer;
}

.AttributeItem--selected {
    background: #FFFFFF;
}

.AttributeItem__image {
    max-width: none;
    width: 100%;
}
.AttributeSelection {
    width: 100%;
    display: flex;
}
.AttributeColorItem {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #808183;
    margin: 2px;
    border-radius: 10px;
    width: 63px;
    height: 63px;
}

.AttributeColorItem:hover {
    cursor: pointer;
}

.AttributeColorItem__check {
    display: inline;
    z-index: 1;
}

.AttributeColorItem__check--remove {
    display: none;
}
.AttributeColorSection {
    display: flex;
    flex-direction: column;
    margin: 8px 0;
}

.AttributeColorSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.AttributeColorSection__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.AttributeTypeItem {
    width: 100px;
    height: 100px;
    border: 2px solid #808183;
    border-radius: 10px;
    margin: 2px;
    padding: 0;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.AttributeTypeItem:hover {
    cursor: pointer;
}

.AttributeTypeItem__check {
    display: inline;
    z-index: 1;
}

.AttributeTypeItem__check--remove {
    display: none;
}

.AttributeTypeItem__image {
    max-width: none;
    width: 100%;
    position: absolute;
}

.AttributeTypeSection {
    display: flex;
    flex-direction: column;
}

.AttributeTypeSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.AttributeTypeSection__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.AttributeOptions {
    display: flex;
    padding: 20px 30px;
    flex-direction: column;
    border-radius: 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
    margin: 10px 0;    
}

.AttributeOptions__title {
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    color: #808183;
    text-align: center;
    margin: 5px 0;
}
.EditPanel {
    width: 100%;
    max-width: 670px;
}
.Creator {
    display: flex;
    width: 100%;
    height: 100%;
    background-color: #9FD1FF;
}
html {
    height: 100%;
}

body {
    margin: 0;
    height: 100%;
}

#root {
    height: 100%;
}

.App {
    height: 100%;
}
